(function ($) {

	"use strict";

	// video overlay hide on play

	$(".play-icon, .video-link").on("click", function(){
		$(".video-overlay").addClass("hidden");
	});

	// video playlist

	$(".video-link").on("click", function(){
		var ths = $(this);
		var videoText = ths.text();
		$(".active-video").text( videoText ).removeClass("menu-active");
		$(".video-links-list").fadeOut( "fast" );
	});

	$(".active-video").on("click", function(){
		$(".video-links-list").fadeToggle( "fast" );
		$(this).toggleClass("menu-active");
	});

	$(document).mouseup(function(e) {
		var container = $(".video-playlist");

		if (!container.is(e.target) && container.has(e.target).length === 0){
			$(".video-links-list").hide();
			$(".active-video").removeClass("menu-active");
		}
	});

}(jQuery));

/** Resources slider */

(function () {

	"use strict";

	// init news and events carousel
	new Swiper(".resources-slider-container", {
		slidesPerView: "auto",
		navigation: false,
		pagination: {
			el: '.resources .swiper-pagination',
			type: 'bullets',
		},
		centeredSlides: false,
		spaceBetween: 30
	});

}());

/** Learn more expand bulleted list */

(function ($) {

	"use strict";

	$(".learn-more").on("click", function () {
		var id = $(this).attr("data-id");
		$(this).addClass("hidden");
		$("."+id).removeClass("hidden");
	});

}(jQuery));

// globe animation

(function () {

	"use strict";

	var animationStarted = false;
	var t = new TimelineLite();
	var ease = window.Power4.easeInOut;
	var edrDiagram = document.querySelector(".edr-diagram") || null;
	var mvedrDiagram = document.querySelector(".multi-vector-edr-diagram") || null;
	var event;

	function timeLineLiteComplete() {
		// trigger custom event to when hero image completes animating
		if (window.CustomEvent) {
			event = new CustomEvent("timeLineLiteCompleteEvent");
		} else {
			event = document.createEvent("CustomEvent");
			event.initCustomEvent("timeLineLiteCompleteEvent", true, true);
		}

		edrDiagram.dispatchEvent(event);
	}

	new Waypoint({
		element: document.getElementById("edr-diagram"),
		offset: "75%",
		handler: function (direction) {
			if (direction === "down" && !animationStarted) {
				animationStarted = true;
				t.fromTo(
					edrDiagram,
					1,
					{
						x: "25%",
						autoAlpha: 0
					},
					{
						x: "0%",
						autoAlpha: 1,
						ease: ease
					}
				);
				t.to(
					edrDiagram,
					1,
					{
						//transform: "rotate(180deg)",
						rotation: "+=180",
						transformOrigin: "center center",
						// autoAlpha: 1,
						// ease: ease,
						onComplete: timeLineLiteComplete
					}
				);
				t.to(
					edrDiagram,
					1,
					{
						autoAlpha: 0
					}
				);
				t.to(
					mvedrDiagram,
					1,
					{
						autoAlpha: 1
					},
					"-=1"
				);
			}
		}
	});

}());